@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
:root{
--sidebar-flex:1.5;
--condiv-flex:6;
--fontsize-p:1.2rem;
--fontsize-nav:1.6rem;
--typing-font:3rem;
}
*
{
margin:0;
padding: 0;
}



body
{
font-family: 'Raleway';
background-image: url(/Portfolio/static/media/felix-besombes-EzrHLVd83h0-unsplash.601eda42.jpg);
background-attachment: fixed;
background-position: center;
background-repeat: none;
background-size: cover;
}

.App
{
display: flex;
width: 100vw;
min-height: 100vh;
}

.container
{
padding: 10px ;
}

ul.no-bullets {
    list-style-type: none; /* Remove bullets */
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margins */
  }

nav
{
padding: 0 ;
flex:1.5 1;
flex:var(--sidebar-flex) 1;
}


